.wadi_dual_haeding_type_inline .wadi_dual_heading_content_text {
  display: inline-block;
  vertical-align: middle;
}

.wadi_dual_haeding_type_block .wadi_dual_heading_content_text {
  display: block;
}

.wadi_dual_heading_container:before {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 0;
  line-height: 1;
  font-size: 30px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.wadi_dual_heading_container .wadi_dual_heading_text_main.wadi_dual_heading_main_text_background_fill {
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.wadi_dual_heading_container .wadi_dual_heading_text_main.wadi_dual_heading_main_text_animated_background {
  -webkit-background-size: 300% 300% !important;
  background-size: 300% 300% !important;
  -webkit-animation: Animated 10s ease-in-out infinite;
  animation: Animated 10s ease-in-out infinite;
}
@-webkit-keyframes Animated {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes Animated {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.wadi_dual_heading_container .wadi_dual_heading_text_before.wadi_dual_heading_highlight_text_background_fill, .wadi_dual_heading_container .wadi_dual_heading_text_after.wadi_dual_heading_highlight_text_background_fill {
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.wadi_dual_heading_container .wadi_dual_heading_text_before.wadi_dual_heading_highlight_text_animated_background, .wadi_dual_heading_container .wadi_dual_heading_text_after.wadi_dual_heading_highlight_text_animated_background {
  -webkit-background-size: 300% 300% !important;
  background-size: 300% 300% !important;
  -webkit-animation: Animated 10s ease-in-out infinite;
  animation: Animated 10s ease-in-out infinite;
}
@-webkit-keyframes Animated {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes Animated {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}